/**
 * body
 */
body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
}

/**
 * container
 */
.container {
  padding: 20px;
}

/**
 * form
 */
.form__row--reverse {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.form__row--between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form__btn {
  display: inline-block;
  padding: 6px 12px;
  border: 1px solid transparent;
  border-color: #0094e4;
  border-radius: 4px;
  background-color: #0094e4;
  color: #fff;
  vertical-align: middle;
  text-align: center;
  white-space: nowrap;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.5;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  user-select: none;
}
.form__btn:hover {
  border-color: #0062cc;
  background-color: #0069d9;
  cursor: pointer;
}
.form__btn:focus, .form__btn:active {
  outline: 0;
  box-shadow: 0 0 3px 3px rgba(0, 148, 228, 0.5);
}

.form__text {
  margin: 0;
  padding: 0;
}

.form__search {
  margin-top: 20px;
}

.form__checkbox {
  position: absolute;
  opacity: 0;
  appearance: none;
}

.form__label {
  position: relative;
  display: block;
  padding-left: 36px;
  background-image: url("data:image/svg+xml;base64,");
  background-position: left center;
  background-size: 24px;
  background-repeat: no-repeat;
  line-height: 44px;
  cursor: pointer;
}
.form__label::after {
  position: absolute;
  top: 50%;
  left: 4px;
  display: block;
  margin-top: -8px;
  width: 16px;
  height: 16px;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
}
.form__checkbox:checked + .form__label::after {
  background-image: url("data:image/svg+xml;base64,");
}
.form__checkbox:focus + .form__label::after, .form__checkbox:active + .form__label::after {
  box-shadow: 0 0 3px 3px rgba(0, 148, 228, 0.5);
}

.is-indeterminateEnabled .form__checkbox:indeterminate + .form__label::after {
  background-image: url("data:image/svg+xml;base64,");
  opacity: 0.5;
}

/**
 * list
 */
.list {
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  border: 1px solid #ddd;
  border-radius: 4px;
  list-style: none;
}

.list__nested {
  margin: 0 0 0 30px;
  padding: 0;
  list-style: none;
}

.list__clickable {
  position: relative;
  box-sizing: border-box;
  margin: 0;
  padding: 0 0 0 15px;
  height: 44px;
  cursor: pointer;
}

.list__line::after {
  position: absolute;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 1px;
  background-color: #eee;
  content: "";
}